        color: red;
        font-size: 15px;;
    }
    .you-tube{
        font-weight: bold;
        color: black;
    }
}
//-----------
.search-container{
    display: flex;
    justify-content: center;
    align-items: center;
    .search-cover{
        width: 65%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #f8f8f8 !important;
        border-radius: 22px;
        padding: 0px 0px 0px 5px;
        margin: 1px;
        border: 1px solid  #f8f8f8 ;
    }
    .search-cover input{
        width: 100% !important;
        border-left: transparent;
        border: none;
        padding: 3px 4px 3px 10px;
        margin: 1px;;
    }
    .search-cover input:focus{
        outline: none;
        // border-bottom-left-radius: 22px;
        // border-top-left-radius: 22px;
    }
    .search-cover button{
        border: none;
        padding: 6px 10px 6px 12px;
        border-top-right-radius: 22px;
        border-bottom-right-radius: 22px;
        font-size: 2px;
    }
    .search-cover .search-1{
        font-size: 5px !important;
    }
}
