.hr{
    border-bottom: 1px solid red;
}
.side-bar-items{
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: start;
    flex-direction: column;
    padding: 5px;
    height: auto;

    .side-bar-content{
        padding: 15px;
        height: 50px;
    }
    .side-bar-content span{
        display: flex;
        height: 26px;
        width:26px;
    }
    .side-bar-content p{
        margin-left: 20px;
    }

    
}
.side-bar-content:hover{
    background-color: gray;
    border-radius: 8px;
}
